<template>
    <!-- Block2 -->
    <div class="block2">
        <div :class="[{'block2-labelnew' : produto.lancamento}, 'block2-img wrap-pic-w of-hidden pos-relative']">

            <img :src="foto" :alt="produto.nome">

            <div class="block2-overlay trans-0-4">
                <router-link :to="{ name: 'produto', params: { id: produto.id }}" class="block2-btn-addwishlist hov-pointer trans-0-4">
                    <i class="icon-wishlist icon_heart_alt" aria-hidden="true"></i>
                    <i class="icon-wishlist icon_heart dis-none" aria-hidden="true"></i>
                </router-link>

                <div class="block2-btn-addcart w-size1 trans-0-4">
                    <!-- Button -->
                    <button class="flex-c-m size1 bg4 bo-rad-23 hov1 s-text1 trans-0-4">
                        Comprar
                    </button>
                </div>
            </div>
        </div>

        <div class="block2-txt p-t-20">
            <router-link :to="{name: 'produto', params: { id: produto.id }}" class="block2-name dis-block s-text3 p-b-5" v-html="produto.nome"></router-link>

            <span class="block2-price m-text6 p-r-5" v-html="produto.precos.preco"></span>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            foto: ''
        }
    },
    props: ['produto'],
    methods: {
        fotoHd() {
            this.foto = this.produto.fotos[0].replace('PEQ_', 'MED_')
        }
    },
    mounted(){
        this.fotoHd();
    }
}
</script>

<style>
    .block2-txt{
        text-align: center
    }
</style>



