<template>

	<!-- Instagram -->
	<section class="instagram p-t-20">

		<div class="sec-title p-b-52 p-l-15 p-r-15">
			<h3 class="m-text5 t-center">
				@ follow us on Instagram
			</h3>
		</div>


		<vue-instagram class="flex-w" token="230271647.1677ed0.26a872f7db58458f947d99ecf5be6c3d" :count="5" mediaType="image">
			<template slot="feeds" slot-scope="props">

				<!-- Block4 -->
				<div class="block4 wrap-pic-w" :style="'background-image: url('+ props.feed.images.standard_resolution.url +')'">
					<!-- <img :src="props.feed.images.standard_resolution.url" alt="IMG-INSTAGRAM"> -->

					<a :href="props.feed.link" class="block4-overlay sizefull ab-t-l trans-0-4">
						<span class="block4-overlay-heart s-text9 flex-m trans-0-4 p-l-40 p-t-25">
							<i class="icon_heart_alt fs-20 p-r-12" aria-hidden="true"></i>
							<span class="p-t-2" v-html="props.feed.likes.count"></span>
						</span>

						<div class="block4-overlay-txt trans-0-4 p-l-40 p-r-25 p-b-30">
							<p class="s-text10 m-b-15 h-size1 of-hidden" v-html="props.feed.caption.text">
							</p>

							<span class="s-text9">
								@{{props.feed.user.username}}
							</span>
						</div>
					</a>
				</div>

			</template>
		</vue-instagram>

	</section>
    
</template>

<script>
import VueInstagram from 'vue-instagram'

export default {
    components: {
		VueInstagram
	}
}
</script>

<style scoped>
	.block4{
		height: 340px;
		background-position: center;
		background-size: cover;
	}
</style>



