<template>
    <!-- Footer -->
    <footer class="bg6 p-t-45 p-b-43 p-l-45 p-r-45">
      <div class="flex-w p-b-90">
        <div class="w-size29 p-t-30 p-l-15 p-r-15 respon3">
          <h4 class="s-text12 p-b-30">
            ENTRE EM CONTATO
          </h4>

          <div>
            <p class="s-text7 w-size27">
              Any questions? Let us know in store at 8th floor, 379 Hudson St, New York, NY 10018 or call us on (+1) 96 716 6879
            </p>

            <div class="flex-m p-t-30" v-if="redesSociais">
              <a v-if="redesSociais.facebook" :href="'facebook.com/' + redesSociais.facebook" class="fs-18 color1 p-r-20 fa fa-facebook"></a>
              <a v-if="redesSociais.google" :href="'google.com/' + redesSociais.google" class="fs-18 color1 p-r-20 fa fa-google"></a>
              <a v-if="redesSociais.instagram" :href="'instagram.com/' + redesSociais.instagram" class="fs-18 color1 p-r-20 fa fa-instagram"></a>
              <a v-if="redesSociais.linkedin" :href="'linkedin.com/' + redesSociais.linkedin" class="fs-18 color1 p-r-20 fa fa-linkedin"></a>
              <a v-if="redesSociais.pinterest" :href="'pinterest.com/' + redesSociais.pinterest" class="fs-18 color1 p-r-20 fa fa-pinterest"></a>
              <a v-if="redesSociais.twitter" :href="'twitter.com/' + redesSociais.twitter" class="fs-18 color1 p-r-20 fa fa-twitter"></a>
              <a v-if="redesSociais.youtube" :href="'youtube.com/' + redesSociais.youtube" class="fs-18 color1 p-r-20 fa fa-youtube"></a>
            </div>
          </div>
        </div>

        <div class="w-size30 p-t-30 p-l-15 p-r-15 respon4">
          <h4 class="s-text12 p-b-30">
            Categorias
          </h4>

          <ul>
            <li v-for="(categoria, index) in categorias" :key="index"
                class="p-b-9">
                <router-link
                :to="{name: 'listagem', params: { id: categoria.id}}"
                v-html="categoria.nome"
                class="s-text13 active1">
                </router-link>
            </li>
          </ul>

        </div>

        <div class="w-size30 p-t-30 p-l-15 p-r-15 respon4">
          <h4 class="s-text12 p-b-30">
            Links
          </h4>

          <ul>
            <li
              v-for="(link, index) in institucional"
              :key="index"
              class="p-b-9">

              <a href="#" class="s-text7">
                Search
              </a>

            </li>
          </ul>
        </div>

        <div class="w-size29 p-t-30 p-l-15 p-r-15 respon3">
          <h4 class="s-text12 p-b-30">
            Newsletter
          </h4>

          <p v-if="retornoNews" v-html="retornoNews.mensagem"></p>

          <form @submit.prevent="cadastraNews">
            <div class="effect1 w-size9">
              <input :value="mail" @input="setNewsEmail" class="s-text7 bg6 w-full p-b-5" type="text" name="email" placeholder="email@exemplo.com">
              <span class="effect1-line"></span>
            </div>

            <div class="w-size2 p-t-20">
              <!-- Button -->
              <button type="submit" class="flex-c-m size2 bg4 bo-rad-23 hov1 m-text3 trans-0-4">
                Cadastrar
              </button>
            </div>

          </form>
        </div>
      </div>

      <div class="t-center p-l-15 p-r-15">
        <a href="#">
          <img class="h-size2" src="theme/images/icons/paypal.png" alt="IMG-PAYPAL">
        </a>

        <a href="#">
          <img class="h-size2" src="theme/images/icons/visa.png" alt="IMG-VISA">
        </a>

        <a href="#">
          <img class="h-size2" src="theme/images/icons/mastercard.png" alt="IMG-MASTERCARD">
        </a>

        <a href="#">
          <img class="h-size2" src="theme/images/icons/express.png" alt="IMG-EXPRESS">
        </a>

        <a href="#">
          <img class="h-size2" src="theme/images/icons/discover.png" alt="IMG-DISCOVER">
        </a>

        <div class="t-center s-text8 p-t-20">
          Copyright  2018 All rights reserved. | This template is made with <i class="fa fa-heart-o" aria-hidden="true"></i> by <a href="https://colorlib.com" target="_blank">Colorlib</a>
        </div>
      </div>
    </footer>
</template>

<script>
    export default {
        methods: {
          setNewsEmail (e) {
            this.$store.commit('setNewsEmail', e.target.value)
          },
          cadastraNews() {
            this.$store.dispatch('cadastraNews')
            .then(() => this.$store.commit('clearEmail'))
          },
        },
        computed: {
            mail() {
              return this.$store.state.newsEmail
            },
            categorias() {
                return this.$store.getters.categorias
            },
            redesSociais() {
              return this.$store.getters.redesSociais
            },
            institucional() {
              return this.$store.getters.institucional
            },
            retornoNews() {
              return this.$store.state.newsRetorno
            }
        }
    }
</script>


