<template>

	<!-- Banner2 -->
	<section class="banner2 bg5 p-t-55 p-b-55">
		<div class="container">
			<div class="row">
				<div v-if="banner" class="col-sm-10 col-md-8 col-lg-8 m-l-r-auto p-t-15 p-b-15">
					<div class="hov-img-zoom pos-relative">
						<img :src="banner[0].imagem" :alt="banner[0].titulo">

						<div class="ab-t-l sizefull flex-col-c-m p-l-15 p-r-15">
							<span class="m-text9 p-t-45 fs-20-sm">
								Especial
							</span>

							<h3 v-html="banner[0].titulo" class="l-text1 fs-35-sm">
							</h3>

							<a href="#" class="s-text4 hov2 p-t-20 ">
								Ver Coleo
							</a>
						</div>
					</div>
				</div>

				<ProdutosDestaque />
				
			</div>
		</div>
	</section>
    
</template>

<script>
import ProdutosDestaque from './ProdutosDestaque.vue'

export default {
	components: {
		ProdutosDestaque
	},
	computed: {
		banner() {
			return this.$store.getters.bannerDestaque
		}
	}
}
</script>



