<template>
	<!-- Banner -->
	<div class="banner bgwhite p-t-40 p-b-40">
		<div class="container">
			<div class="row">

				<template v-for="banner in banners">

					<div :key="banner.id" class="col-sm-10 col-md-8 col-lg-4 m-l-r-auto">
						<!-- block1 -->
						<div class="block1 hov-img-zoom pos-relative m-b-30">
							<img :src="banner.imagem" :alt="banner.titulo">

							<div class="block1-wrapbtn w-size2">
								<!-- Button -->
								<a :href=" banner.url.length ? banner.url : '#' " class="flex-c-m size2 m-text2 bg3 hov1 trans-0-4" v-html="banner.titulo">
								</a>
							</div>
						</div>
					</div>

				</template>
				
			</div>
		</div>
	</div>
</template>

<script>
export default {
	computed: {
		banners () {
			return this.$store.getters.bannerMini
		}
	},
	created () {
		this.$store.dispatch('fetchBanners')
	}
}
</script>



