import Vue from 'vue'
import Vuex from 'vuex'

import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        idLoja: '8284',
        tokenLoja: '0-2018092515562509654895156-1561516516518284-3245698441682842018',
        categorias: {},
        banners: {},
        produtosVitrine: {},
        produtosGrupos: {},
        produtosListagem: {},
        produtosRelacionados: {},
        produtosDestaque: {},
        detalhesProduto: {},
        infoLojas: {},
        newsEmail: '',
        newsRetorno: {},
    },
    
    mutations: {
        setCategorias (state, categorias) {
            state.categorias = categorias
        },
        setBanners (state, banners){
            state.banners = banners
        },
        setProdutosVitrine (state, produtos) {
            state.produtosVitrine = produtos
        },
        setProdutosGrupos (state, grupos) {
            state.produtosGrupos = grupos
        },
        setProdutosListagem (state, produtos) {
            state.produtosListagem = produtos
        },
        setProdutosRelacionados (state, produtos) {
            state.produtosRelacionados = produtos
        },
        setProdutosDestaque (state, produtos) {
            state.produtosDestaque = produtos
        },
        setDetalhesProduto (state, produto) {
            state.detalhesProduto = produto
        },
        setInfoLojas (state, infos) {
            state.infoLojas = infos
        },
        setNewsEmail (state, email) {
            state.newsEmail = email
        },
        setNewsRetorno (state, message) {
            state.newsRetorno = message
        },
        clearEmail (state) {
            state.newsEmail = ''
        }
    },

    getters: {
        bannerTopo (state) {
            if(state.banners.length){
                return state.banners.filter(banner => banner.tipo == 'topo')
            }
        },
        bannerMini (state) {
            if(state.banners.length){
                return state.banners.filter(banner => banner.tipo == 'mini')
            }
        },
        bannerDestaque (state) {
            if(state.banners.length){
                return state.banners.filter(banner => banner.tipo == '8')
            }
        },
        menuPersonalizado (state) {
            return state.categorias.MenuPersonalizado
        },
        categorias (state) {
            return state.categorias.Categorias
        },
        filtros (state) {
            return state.categorias.Filtros
        },
        redesSociais (state) {
            return state.infoLojas.redes_sociais
        },
        institucional (state) {
            return state.infoLojas.menuinstitucional
        },
        cliente (state) {
            return state.infoLojas.cliente
        }
    },
    
    actions: {
        fetchCategorias ({commit}) {
            axios.post(
                'https://devtestews.wslojas.com.br/api-loja/categorias',
                `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}&VarsCategorias=_____False_`
                )
                .then(response => (
                    commit('setCategorias', response.data)
                ));
        },
        fetchBanners ({commit}) {
            return new Promise((resolve) => {
                axios.post(
                    'https://devtestews.wslojas.com.br/api-loja/banners',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}`
                )
                .then(response => (
                    commit('setBanners', response.data.banners),
                    resolve()
                ));
            })
        },
        fetchProdutosVitrine ({commit}) {
            return new Promise((resolve) => {
                axios.post(
                    'https://devtestews.wslojas.com.br/api-loja/produtos/home',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}`
                )
                .then(response => (
                    commit('setProdutosVitrine', response.data.produtos),
                    resolve()
                ));
            })
        },
        fetchProdutosGrupos ({commit}) {
            return new Promise((resolve) => {
                axios.post(
                    'https://devtestews.wslojas.com.br/api-loja/produtos/grupos',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}`
                )
                .then(response => (
                    commit('setProdutosGrupos', response.data.grupos),
                    resolve()
                ));
            })
        },
        fetchProdutosListagem ({commit}, payload) {
            return new Promise((resolve) => {
                axios
                .post(
                    'https://devtestews.wslojas.com.br/api-loja/produtos/listagem',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}&InfoListagem=_|_${payload}_|__|__|__|__|__|_PROD.PROD_NOME_|__|__|__|__|__|_`
                )
                .then(response => (
                    commit('setProdutosListagem', response.data),
                    resolve()
                ));
            })
        },
        fetchProdutosRelacionados ({commit}, payload) {
            return new Promise((resolve) => {
                axios
                .post(
                    'https://devtestews.wslojas.com.br/api-loja/produtos/relacionados',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}&Produto=${payload}`
                )
                .then(response => (
                    commit('setProdutosRelacionados', response.data),
                    resolve()
                ));
            })
        },
        fetchProdutosDestaque ({commit}) {
            return new Promise((resolve) => {
                axios
                .post(
                    'https://devtestews.wslojas.com.br/api-loja/produtos/destaques',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}`
                )
                .then(response => (
                    commit('setProdutosDestaque', response.data.produtos),
                    resolve()
                ));
            })
        },
        fetchDetalhesProduto ({commit}, payload) {
            return new Promise((resolve) => {
                axios
                .post(
                    'https://devtestews.wslojas.com.br/api-loja/produtos/dadosproduto',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}&Produto=${payload}`
                )
                .then(response => (
                    commit('setDetalhesProduto', response.data),
                    resolve()
                ));
            })
        },
        fetchInfoLojas ({commit}) {
            return new Promise((resolve) => {
                axios
                .post(
                    'https://devtestews.wslojas.com.br/api-loja/InfosLojas',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}`
                )
                .then(response => (
                    commit('setInfoLojas', response.data),
                    resolve()
                ));
            })
        },
        cadastraNews ({commit}) {
            return new Promise((resolve) => {
                axios
                .post(
                    'https://devtestews.wslojas.com.br/api-loja/cadastra-news',
                    `LOJA=${this.state.idLoja}&LvToken=${this.state.tokenLoja}&Nome=cliente&Email=${this.state.newsEmail}`
                )
                .then(response => (
                    commit('setNewsRetorno', response.data),
                    resolve()
                ));
            })
        }
    }
})



