<template>
    <!-- Menu -->
    <div class="wrap_menu">
    <nav class="menu">
        <ul class="main_menu">
            <template v-for="(categoria, index) in categorias">
                <li :key="index">
                    <router-link
                    :to="categoria.tipo == 'dpt' ? {name: 'listagem', params: { id: categoria.registro}} : {name:'home'}"
                    v-html="categoria.nome">
                    </router-link>
                </li>
            </template>

            <!-- <li>
                <a href="index.html">Home</a>
                <ul class="sub_menu">
                <li><a href="index.html">Homepage V1</a></li>
                <li><a href="home-02.html">Homepage V2</a></li>
                <li><a href="home-03.html">Homepage V3</a></li>
                </ul>
            </li> -->

        </ul>
    </nav>
    </div>
</template>

<script>
export default {
    computed: {
        categorias() {
            return this.$store.getters.menuPersonalizado
        }
    },
    created() {
        this.$store.dispatch('fetchCategorias')
    }
}
</script>



