<template>

<!-- Slide1 -->
	<section class="slide1">
		<div class="wrap-slick1">
			<div class="slick1">

				<template v-for="banner in banners">

					<div
						:key="banner.id"
						v-if="banner.tipo == 'topo'"
						class="item-slick1 item1-slick1"
						:style="`background-image: url(${banner.imagem})`">

						<div class="wrap-content-slide1 sizefull flex-col-c-m p-l-15 p-r-15 p-t-150 p-b-170">
							<!-- <span
								class="caption1-slide1 m-text1 t-center animated visible-false m-b-15"
								data-appear="fadeInDown"
								v-html="banner.titulo">
							</span> -->

							<h2
								class="caption2-slide1 xl-text1 t-center animated visible-false m-b-37"
								data-appear="fadeInUp"
								v-html="banner.titulo">
							</h2>

							<div class="wrap-btn-slide1 w-size1 animated visible-false" data-appear="zoomIn">
								<!-- Button -->
								<a href="product.html" class="flex-c-m size2 bo-rad-23 s-text2 bgwhite hov1 trans-0-4">
									Comprar
								</a>
							</div>
						</div>
					</div>

				</template>

			</div>
		</div>
	</section>
    
</template>

<script>
export default {
	computed: {
		banners() {
			return this.$store.getters.bannerTopo
		}
	},
	
	methods: {
		Slick: function() {

			setTimeout(function(){

				/*[ Slick1 ]
				===========================================================*/
				var itemSlick1 = $('.slick1').find('.item-slick1');
				var action1 = [];
				var action2 = [];
				var action3 = [];
				var cap1Slide1 = [];
				var cap2Slide1 = [];
				var btnSlide1 = [];

				for(var i=0; i<itemSlick1.length; i++) {
				cap1Slide1[i] = $(itemSlick1[i]).find('.caption1-slide1');
				cap2Slide1[i] = $(itemSlick1[i]).find('.caption2-slide1');
				btnSlide1[i] = $(itemSlick1[i]).find('.wrap-btn-slide1');
				}


				$('.slick1').on('init', function(){

					action1[0] = setTimeout(function(){
						$(cap1Slide1[0]).addClass($(cap1Slide1[0]).data('appear') + ' visible-true');
					},200);

					action2[0] = setTimeout(function(){
						$(cap2Slide1[0]).addClass($(cap2Slide1[0]).data('appear') + ' visible-true');
					},1000);

					action3[0] = setTimeout(function(){
						$(btnSlide1[0]).addClass($(btnSlide1)[0].data('appear') + ' visible-true');
					},1800);              
				});


				$('.slick1').slick({
					slidesToShow: 1,
					slidesToScroll: 1,
					fade: true,
					dots: false,
					appendDots: $('.wrap-slick1-dots'),
					dotsClass:'slick1-dots',
					infinite: true,
					autoplay: true,
					autoplaySpeed: 6000,
					arrows: true,
					appendArrows: $('.wrap-slick1'),
					prevArrow:'<button class="arrow-slick1 prev-slick1"><i class="fa  fa-angle-left" aria-hidden="true"></i></button>',
					nextArrow:'<button class="arrow-slick1 next-slick1"><i class="fa  fa-angle-right" aria-hidden="true"></i></button>',  
				});

				$('.slick1').on('afterChange', function(event, slick, currentSlide){ 
					for(var i=0; i<itemSlick1.length; i++) {

					clearTimeout(action1[i]);
					clearTimeout(action2[i]);
					clearTimeout(action3[i]);


					$(cap1Slide1[i]).removeClass($(cap1Slide1[i]).data('appear') + ' visible-true');
					$(cap2Slide1[i]).removeClass($(cap2Slide1[i]).data('appear') + ' visible-true');
					$(btnSlide1[i]).removeClass($(btnSlide1[i]).data('appear') + ' visible-true');

					}

					action1[currentSlide] = setTimeout(function(){
						$(cap1Slide1[currentSlide]).addClass($(cap1Slide1[currentSlide]).data('appear') + ' visible-true');
					},200);

					action2[currentSlide] = setTimeout(function(){
						$(cap2Slide1[currentSlide]).addClass($(cap2Slide1[currentSlide]).data('appear') + ' visible-true');
					},1000);

					action3[currentSlide] = setTimeout(function(){
						$(btnSlide1[currentSlide]).addClass($(btnSlide1)[currentSlide].data('appear') + ' visible-true');
					},1800);            
				});

			}, 0);


		}
	},

	created() {
		this.$store.dispatch('fetchBanners')
		.then(() => this.Slick())
	}
}
</script>



