<template>

	<!-- Our product -->
	<section class="bgwhite p-t-45 p-b-58">
		<div class="container">
			<div class="sec-title p-b-22">
				<h3 class="m-text5 t-center">
					Grupos de Produto
				</h3>
			</div>

			<!-- Tab01 -->
			<div class="tab01">
				<!-- Nav tabs -->
				<ul class="nav nav-tabs" role="tablist">

					<li
						v-for="(grupo, index) in grupos"
						:key="index"
						class="nav-item">
						
						<a v-if="grupo"
							:class="[index == 0 ? 'active' : '', 'nav-link']"
							data-toggle="tab" 
							:href="'#' + grupo.codigo" 
							role="tab"
							v-html="grupo.nome">
						</a>

					</li>

				</ul>

				<!-- Tab panes -->
				<div class="tab-content p-t-35">
					<!-- - -->
					<div
						v-for="(grupo, index) in grupos"
						:key="index"
						:class="[index == 0 ? 'active show' : '', 'tab-pane fade']"
						:id="grupo.codigo"
						role="tabpanel">

						<div class="row">


							<div class="col-sm-6 col-md-4 col-lg-3 p-b-50"
								v-for="(produto, index) in grupo.produtos"
								:key="index">
								<!-- Block2 -->
								<BlocoProduto :produto="produto" />
								
							</div>

						</div>
					</div>

				</div>
			</div>
		</div>
	</section>
    
</template>

<script>
import axios from 'axios'
import BlocoProduto from '../geral/BlocoProduto.vue'

export default {
	computed: {
		grupos () {
			return this.$store.state.produtosGrupos
		}
	},
	components: {
		BlocoProduto
	},
	created() {
		this.$store.dispatch('fetchProdutosGrupos')
	}
}
</script>



