<template>
<div>

    <transition name="fade">
        <Slider />
    </transition>
    
    <Banner1 />

    <ProdutosVitrine />

    <Banner2 />

    <ProdutosGrupos />

    <Instagram />


</div>
</template>

<script>
import Slider from '../components/home/Slider.vue'
import Banner1 from '../components/home/Banner1.vue'
import Banner2 from '../components/home/Banner2.vue'
import ProdutosVitrine from '../components/home/ProdutosVitrine.vue'
import ProdutosGrupos from '../components/home/ProdutosGrupos.vue'
import Instagram from '../components/home/Instagram.vue'


export default {
    data () {
        return {}
    },
    components: {
        Slider,
        Banner1,
        ProdutosVitrine,
        Banner2,
        ProdutosGrupos,
        Instagram
    }
}
</script>



