<template>
    <!-- Relate Product -->
	<section class="relateproduct bgwhite p-t-45 p-b-138">
		<div class="container">
			<div class="sec-title p-b-60">
				<h3 class="m-text5 t-center">
					Produtos Relacionados
				</h3>
			</div>

			<!-- Slide2 -->
			<div class="wrap-slick2">
				<div id="slider-related" class="slick2">

                    <template v-for="(produto, index) in produtos">

                        <BlocoProduto :key="index" :produto="produto" />
                        
                    </template>

				</div>
			</div>

		</div>
	</section>
</template>

<script>
import BlocoProduto from '../geral/BlocoProduto.vue'

export default {
    computed: {
        produtos() {
            return this.$store.state.produtosRelacionados
        }
    },
    components: {
        BlocoProduto
    },
    methods: {
        Slick() {
            $('#slider-related').slick({
                slidesToShow: 4,
                slidesToScroll: 4,
                infinite: true,
                autoplay: false,
                autoplaySpeed: 6000,
                arrows: true,
                appendArrows: $('.wrap-slick2'),
                prevArrow:'<button class="arrow-slick2 prev-slick2"><i class="fa  fa-angle-left" aria-hidden="true"></i></button>',
                nextArrow:'<button class="arrow-slick2 next-slick2"><i class="fa  fa-angle-right" aria-hidden="true"></i></button>',  
                responsive: [
                    {
                    breakpoint: 1200,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 4
                    }
                    },
                    {
                    breakpoint: 992,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3
                    }
                    },
                    {
                    breakpoint: 768,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2
                    }
                    },
                    {
                    breakpoint: 576,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                    }
                ]    
            });
        }
    },
    created() {
        if(this.$route.params.id){
            this.$store.dispatch('fetchProdutosRelacionados', this.$route.params.id)
            .then(() => this.Slick())
        }
    }
}
</script>




