<template>
<div class="col-sm-10 col-md-8 col-lg-4 m-l-r-auto p-t-15 p-b-15" id="slider-destaque-wrap">
    <div id="slider-destaque">

        <div
            v-for="(produto, index) in produtos"
            :key="index"
            class="bgwhite hov-img-zoom pos-relative p-b-20per-ssm">

            <img :src="produto.fotos[0].replace('PEQ_', 'MED_')" :alt="produto.nome">

            <div class="produto-detalhes ab-b-l flex-col-c-b p-l-15 p-r-15 p-b-20">
                <div class="t-center">
                    <router-link
                    :to="{name: 'produto', params: { id: produto.id }}"
                    v-html="produto.nome"
                    class="dis-block s-text3 p-b-5">
                    </router-link>

                    <span class="block2-oldprice m-text7 p-r-5">
                        $29.50
                    </span>

                    <span class="block2-newprice m-text8">
                        $15.90
                    </span>
                </div>
            </div>
        </div>

    </div>
</div>
</template>

<script>
export default {
    computed: {
        produtos() {
            return this.$store.state.produtosDestaque
        }
    },
    methods: {
        Slick() {
            $('#slider-destaque').slick({
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                autoplay: true,
                autoplaySpeed: 3000,
                arrows: false,
                appendArrows: $('#slider-destaque-wrap'),
                prevArrow:'<button class="arrow-slick2 prev-slick2"><i class="fa  fa-angle-left" aria-hidden="true"></i></button>',
                nextArrow:'<button class="arrow-slick2 next-slick2"><i class="fa  fa-angle-right" aria-hidden="true"></i></button>'
            })
        }
    },
    created() {
        this.$store.dispatch('fetchProdutosDestaque')
        .then(() => this.Slick())
	}
}
</script>

<style scoped>
    .produto-detalhes{
        background-color: rgba(255, 255, 255, 0.8);
        padding: 30px 0;
        width: 100%;
    }
    .dis-block{
        color: #222;
        font-weight: 500;
        font-size: 16px;
    }
</style>



