<template>

	<!-- New Product -->
	<section class="newproduct bgwhite p-t-45 p-b-105">
		<div class="container">
			<div class="sec-title p-b-60">
				<h3 class="m-text5 t-center">
					Produtos da Vitrine
				</h3>
			</div>

			<!-- Slide2 -->
			<div class="wrap-slick2">
				<div class="slick2">
					<div class="item-slick2 p-l-15 p-r-15"
					v-if="produtos.length"
					v-for="produto in produtos"
					:key="produto.id">

						<BlocoProduto :produto="produto"/>
						
					</div>

				</div>
			</div>

		</div>
	</section>
    
</template>

<script>
import BlocoProduto from '../geral/BlocoProduto.vue'

export default {
	computed: {
		produtos() {
			return this.$store.state.produtosVitrine
		}
	},
	components: {
		BlocoProduto
	},
	methods: {
		Slick() {

			/*[ Slick2 ]
			===========================================================*/
			$('.slick2').slick({
				slidesToShow: 4,
				slidesToScroll: 4,
				infinite: true,
				autoplay: false,
				autoplaySpeed: 6000,
				arrows: true,
				appendArrows: $('.wrap-slick2'),
				prevArrow:'<button class="arrow-slick2 prev-slick2"><i class="fa  fa-angle-left" aria-hidden="true"></i></button>',
				nextArrow:'<button class="arrow-slick2 next-slick2"><i class="fa  fa-angle-right" aria-hidden="true"></i></button>',  
				responsive: [
					{
					breakpoint: 1200,
					settings: {
						slidesToShow: 4,
						slidesToScroll: 4
					}
					},
					{
					breakpoint: 992,
					settings: {
						slidesToShow: 3,
						slidesToScroll: 3
					}
					},
					{
					breakpoint: 768,
					settings: {
						slidesToShow: 2,
						slidesToScroll: 2
					}
					},
					{
					breakpoint: 576,
					settings: {
						slidesToShow: 1,
						slidesToScroll: 1
					}
					}
				]    
			});

		}
	},
	created() {
		this.$store.dispatch('fetchProdutosVitrine')
		.then(() => this.Slick())
	}
}
</script>



