import Vue from 'vue'
import Axios from 'axios';
import VueInstagram from 'vue-instagram'

import App from './App.vue'
import router from './router'
import store from './store'

Vue.use(Axios)
Vue.use(VueInstagram)

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



